body {
  background-color: #dcc8de;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  background: #ebd8f4;
  border-radius: 8px;
  max-width: 960px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

h1, h2 {
  text-align: center;
}

table {
  border-collapse: collapse;
  width: 100%;
  margin: 20px 0;
}

th, td {
  text-align: center;
  border: 1px solid #af55a8;
  padding: 8px;
}

th {
  background-color: #f1dcf3;
}

button {
  color: #595959;
  cursor: pointer;
  background-color: #faf5b7;
  border: none;
  border-radius: 10px;
  margin: 10px;
  padding: 8px 12px;
}

button:hover {
  background-color: #ddda7a;
}

form {
  margin-top: 20px;
}

form label {
  margin-bottom: 8px;
  display: block;
}

form input {
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 10px;
  padding: 6px;
}

.modal {
  z-index: 1000;
  background-color: #00000080;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  background-color: #fff;
  border-radius: 12px;
  width: 90%;
  max-width: 500px;
  padding: 20px;
  position: relative;
  top: 15%;
  left: 33%;
  box-shadow: 0 4px 10px #0000004d;
}
